import { Link, useLoaderData } from "remix";
import { getLessons } from "~/lesson";

export const loader = () => {
  return getLessons();
};

export default function Lessons() {
  const lessons = useLoaderData();
  console.log(lessons);
  return (
    <div>
      <h1>Lessons!?</h1>
      <ul>
        {lessons.map(post => (
          <li key={post.slug}>
            <Link to={post.fullSlug}>{post.title}</Link>
          </li>
        ))}
      </ul>
    </div>
  );
}